//
//Padding and Margin
//

$padding: "p";
$margin: 'm';

@each $label, $value in map-get($theme, 'padding') {

    .#{$padding}-#{$label} {padding: $value;}
    .#{$padding}t-#{$label} {padding-top: $value;}
    .#{$padding}b-#{$label} {padding-bottom: $value;}
    .#{$padding}l-#{$label} {padding-left: $value;}
    .#{$padding}r-#{$label} {padding-right: $value;}

    .#{$padding}x-#{$label} {
        padding-left: $value;
        padding-right: $value;
    }

    .#{$padding}y-#{$label} {
        padding-top: $value;
        padding-bottom: $value;
    }

  
  
}

@each $label, $value in map-get($theme, 'margin') {
    
    .#{$margin}-#{$label} {margin: $value;}
    .#{$margin}t-#{$label} {margin-top: $value;}
    .#{$margin}b-#{$label} {margin-bottom: $value;}
    .#{$margin}l-#{$label} {margin-left: $value;}
    .#{$margin}r-#{$label} {margin-right: $value;}

    .#{$margin}x-#{$label} {
        margin-left: $value;
        margin-right: $value;
    }

    .#{$margin}y-#{$label} {
        margin-top: $value;
        margin-bottom: $value;
    }
  
}

